import { useLocation } from 'react-router-dom'
import { Typography, Collapse, Card } from 'antd'
import { QuestionCircleOutlined } from '@ant-design/icons'
import PageNavigation from '@/components/PageNavigation'
import { getPageNavigation } from '@/utils/pageOrder'
import { useTheme } from '@/contexts/ThemeContext'
import CodeBlock from '@/components/CodeBlock'

const { Title, Paragraph } = Typography

const questions = [
  {
    q: '有问题如何反馈？',
    a: '您可以通过 GitHub Issues 提交问题反馈。建议在提交时附上详细的问题描述、复现步骤及相关截图，以便我们更快定位和解决问题。',
    extra: (
      <div className="mt-4">
        <a href="https://github.com/TME-Sloth/d2c/issues" target="_blank" rel="noopener noreferrer" className="text-primary hover:underline">
          https://github.com/TME-Sloth/d2c/issues
        </a>
      </div>
    ),
  },
  {
    q: '进程卡住或者拦截页显示需要身份验证？',
    a: '这通常是由于 MCP 服务端口被占用或进程异常导致的。您可以通过以下命令清理 3100-3199 端口范围内的残留进程来解决此问题：',
    extra: <CodeBlock language="bash">{'for port in {3100..3199}; do lsof -ti:$port | xargs -r kill -9; done 2>/dev/null || true'}</CodeBlock>,
  },
  {
    q: '若 AI Agent 不支持采样，圈选分组、组件映射、模块映射等高级功能还有用吗？',
    a: '依然有效。即使 AI Agent 不支持采样功能，MCP 服务仍会将圈选分组、组件映射、模块映射等配置信息整合到转码提示词中，供 AI Agent 执行转码任务。区别在于：支持采样时可实现更精细的交互式转码流程，转码效率和准确性会更高。',
  },
  {
    q: '我的项目用的是 Tailwind CSS / CSS Modules / Styled-components，支持吗？',
    a: '支持。D2C 兼容主流的 CSS 方案。您可以在转码拦截页中配置自定义的转码规则，将项目的样式规范（如 Tailwind 类名约定、CSS Modules 命名规范等）融入转码提示词，确保生成的代码符合您的工程规范。',
  },
]

const FAQ = () => {
  const location = useLocation()
  const pageNav = getPageNavigation(location.pathname)
  const { isDark } = useTheme()

  const items = questions.map((item, index) => ({
    key: String(index + 1),
    label: (
      <span className="font-medium">
        {index + 1}、{item.q}
      </span>
    ),
    children: (
      <div>
        <Paragraph className="!mb-0">{item.a}</Paragraph>
        {item.extra}
      </div>
    ),
  }))

  return (
    <div className={`p-6 md:p-8 max-w-4xl mx-auto ${isDark ? 'bg-[#141414]' : 'bg-white'}`}>
      <div className="markdown-content">
        <Title level={1}>常见问题</Title>

        <Card className={`mt-8 mb-6 ${isDark ? 'bg-blue-900/20 border-blue-800' : 'bg-blue-50 border-blue-100'}`}>
          <div className="flex items-start gap-3">
            <QuestionCircleOutlined className="text-2xl text-primary mt-1" />
            <div>
              <Title level={5} className="!mt-0 !mb-1">
                遇到问题？
              </Title>
              <Paragraph className={`!mb-0 ${isDark ? 'text-gray-300' : 'text-gray-600'}`}>
                如果您在使用过程中遇到任何问题，可以通过以下方式获取帮助。我们会尽快为您解答。
              </Paragraph>
            </div>
          </div>
        </Card>

        <Collapse
          items={items}
          defaultActiveKey={['1']}
          className={
            isDark
              ? 'bg-[#1f1f1f] [&_.ant-collapse-item]:border-gray-700 [&_.ant-collapse-content]:bg-[#141414] [&_.ant-collapse-header]:!text-gray-200'
              : 'bg-white'
          }
          expandIconPosition="end"
        />

        {/* 页面导航 */}
        <PageNavigation prev={pageNav.prev} next={pageNav.next} />
      </div>
    </div>
  )
}

export default FAQ
